.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.time-control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 50%;
  input {
    width: 100%;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    cursor: pointer;
    border: none;

    &:focus {
      outline: none;
    }
    &::-webkit-slider-thumb {
      -webkit-appearance: none;
    }
    &::-moz-range-thumb {
      -webkit-appearance: none;
      background: transparent;
      border: none;
    }
  }
  p {
    padding: 1rem;
  }
}
.play-control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 30%;
  svg {
    cursor: pointer;
  }
}

.track {
  background: rgb(62, 135, 219);
  width: 100%;
  height: 1rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  border: none;
  display: inline-block;
}
.animate-track {
  width: 100%;
  height: 100%;
  height: 1.5rem;
  background: rgb(218, 210, 210);
  position: absolute;
  top: 0;
  border: none;
  transform: translateX(0%);
  pointer-events: none;
}

@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }
  .play-control {
    width: 60%;
  }
}
