.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  background: white;
  box-shadow: 10px 10px 50px rgb(204, 204, 204);
  overflow-y: scroll;
  transform: translateX(-100%);
  opacity: 0;
  transition: all 0.5s ease;
  h2 {
    padding: 2rem 1rem;
  }
}
.library-song {
  display: flex;
  align-items: center;
  padding: 1rem;
  cursor: pointer;

  &:hover {
    background: rgb(163, 181, 214);
  }
  img {
    width: 30%;
  }
}
.title {
  padding-left: 1rem;
  h3 {
    font-size: 1.1rem;
    margin-bottom: 0.3rem;
    color: rgb(100, 100, 100);
    font-weight: 400;
  }
  h4 {
    font-size: 0.8rem;
    color: rgb(100, 100, 100);
    font-weight: 400;
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}

*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}
*::-webkit-scrollbar-thumb {
  background-color: rgba(155, 155, 155, 0.5);
  border-radius: 20px;
  border: transparent;
}

.selected {
  background: rgb(118, 152, 214);
}

.active-library {
  transform: translateX(0%);
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .library {
    width: 100%;
  }
}
