nav {
  min-height: 10vh;
  width: 90%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  h1 {
    font-size: 2.1rem;
    color: cornflowerblue;
  }
  button {
    padding: 0.4rem 0.8rem;
    font-size: 1.2rem;
    border: 1px solid cornflowerblue;
    cursor: pointer;
    font-weight: 400;
    background: cornflowerblue;
    color: white;

    &:hover {
      background: rgb(109, 141, 199);
    }
  }
}

@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 10;
    }
  }
}
