.song-container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  img {
    width: 20rem;
    border-radius: 50%;
    margin-bottom: 1.5rem;
  }
  h1 {
    margin-bottom: 1rem;
    color: cornflowerblue;
  }
}

@media screen and (max-width: 768px) {
  .song-container {
    img {
      width: 60%;
    }
  }
}
